.AboutMe {
  padding: 2.5rem 0 2.5rem 0;
  width: 100%;
  margin: 0px;
  min-height: 85vh;

  h1 {
    margin-top: 0;
  }

  img {
    height: 300px;
    width: 300px;
    border-radius: 50%;
  }

  ul {
    list-style: none;
  }

  .Description {
    font-size: 18px;
    font-family: "Open Sans", sans-serif;

    p {
      padding: 0 2.5rem 0 2.5rem;
    }

    .Interests {
      padding: 0 2.5rem 0 2.5rem;
    }

    .SkillGrid {
      display: grid;
      margin: 0 2.5rem 0 5rem;
      text-align: center;
      grid-gap: 1.5rem;
      grid-template-columns: repeat(4, calc(100% / 6));
      grid-auto-rows: auto;
      grid-auto-rows: minmax(10px, auto);
      justify-content: center;
      div {
        margin: 0 5% 0 5%;
      }
    }
  }
}

@media screen and (min-width: 499px) and (max-width: 800px) {
  .AboutMe {
    .Description {
      .SkillGrid {
        grid-template-columns: repeat(2, calc(100% / 3));
        grid-gap: 2.5rem;
        grid-auto-rows: auto;
        grid-auto-rows: minmax(5px, auto);
        justify-content: center;
        div {
          margin-right: 1rem;
        }
      }
    }
  }
}

@media screen and (min-width: 299px) and (max-width: 499px) {
  .AboutMe {
    .Description {
      .SkillGrid {
        margin-right: 2.5rem;
        grid-template-columns: repeat(2, calc(100% / 1.5));
        grid-auto-rows: auto;
        grid-gap: 0rem;
        grid-auto-rows: minmax(5px, auto);
        justify-content: center;
      }
      p {
        padding: 0;
      }
    }
  }
}
