.Card {
  width: 75%;
  padding: 0.25rem;
  border-radius: 5%;
  transform: scale(1);
  transition: 0.2s ease-in-out;
  p {
    margin-top: 0;
    // padding: 0 2rem 0 0;
  }
  svg {
    margin-top: 1.5rem;
  }
  a {
    transform: scale(1);
    transition: 0.2s ease-in-out;
  }
  a:hover {
    cursor: pointer;
    transform: scale(1.15);
    transition: 0.2s ease-in-out;
  }
}

.Card:hover {
  transform: scale(1.05);
  transition: 0.2s ease-in-out;
}

.LightCard {
  a,
  a:visited {
    color: black;
  }
}

.LightCard:hover {
  box-shadow: 0 0.1rem 0.5rem black;
}

.DarkCard {
  a,
  a:visited {
    color: white;
  }
}

.DarkCard:hover {
  box-shadow: 0 0.1rem 0.5rem white;
}
